events.on('ready', function() {
    check();

    $('.btn1').on('click', function() {
        $(this).removeClass('animated');
        $('.map').addClass('hide');
        $('.map1').removeClass('hide');
        $('.map1').focus();
    });
    $('.btn2').on('click', function() {
        $('.map').addClass('hide');
        $('.map2').removeClass('hide');
        $('.map2').focus();
    });
    $('.btn3').on('click', function() {
        $('.map').addClass('hide');
        $('.map3').removeClass('hide');
        $('.map3').focus();
    });
    $('.btn4').on('click', function() {
        $('.map').addClass('hide');
        $('.map').removeClass('hide');
        $('.map4').focus();
    });

    function check() {
        if( scorm.loadObject('lm1') == true ) {
            $('.btn1').addClass('visited');
            $('.btn2').removeClass('block');
            // $('.mapa0').addClass('hide');
            // $('.mapa1').removeClass('hide');
        }
        if( scorm.loadObject('lm2') == true ) {
            $('.btn2').addClass('visited');
            $('.btn3').removeClass('block');
            // $('.mapa1').addClass('hide');
            // $('.mapa2').removeClass('hide');
        }
        if( scorm.loadObject('lm3') == true ) {
            $('.btn3').addClass('visited');
            $('.btn4').removeClass('block');
            // $('.mapa2').addClass('hide');
            // $('.mapa3').removeClass('hide');
        }
        if( scorm.loadObject('lm4') == true ) {
            $('.btn4').addClass('visited');
            $('.arrow-right').removeClass('hide');
            // $('.mapa3').addClass('hide');
            // $('.mapa4').removeClass('hide');
            scorm.setCompleted();
        }
    }

});